import React, { useEffect } from "react";
import { useCart } from "../../contexts/CartContext"; // Import useCart  ly d liu t CartContext
import { useNavigate } from "react-router-dom";
import styles from "../../styles/customer/CMenuPage.module.css"; // Gi s bn c file CSS ny
import C_Footer from "../../components/customer/C_Footer";
import C_Header from "../../components/customer/C_Header";

const OM_C_Cart = () => {
  const { state, dispatch } = useCart(); // Ly state v dispatch t CartContext
  const navigate = useNavigate();

  // Hm x l thay i s lng
  const handleQuantityChange = (id, quantity) => {
    dispatch({
      type: "UPDATE_QUANTITY",
      payload: { id, quantity },
    });
  };

  // Hm x l xa sn phm khi gi hng
  const handleRemoveFromCart = (id) => {
    dispatch({
      type: "REMOVE_FROM_CART",
      payload: { id },
    });
  };

  // Hm x l thanh ton
  const handleCheckout = () => {
    navigate("/checkout");
  };

  return (
    <div className={styles["cart-container"]}>
      <C_Header />
      <h2>Your Cart</h2>
      <div className={styles["product-grid"]}>
        {state.items.length === 0 ? (
          <p>Your cart is empty.</p>
        ) : (
          state.items.map((item) => (
            <div key={item.id} className={`${styles["product-card"]}`}>
              <button
                onClick={() => handleRemoveFromCart(item.id)}
                className={styles["remove-btn"]}
              >
                x
              </button>
              <img
                src={item.image}
                alt={item.name}
                className={styles["product-img"]}
              />
              <div className={styles["product-info"]}>
                <h3 className={styles["product-name"]}>{item.name}</h3>
                <span className={styles["product-price"]}>${item.price}</span>
                <div className={`${styles["quantity-controls"]}`}>
                  <button
                    onClick={() =>
                      handleQuantityChange(item.id, item.quantity - 1)
                    }
                    disabled={item.quantity <= 1}
                  >
                    -
                  </button>
                  <span>{item.quantity}</span>
                  <button
                    onClick={() =>
                      handleQuantityChange(item.id, item.quantity + 1)
                    }
                  >
                    +
                  </button>
                </div>
                
              </div>
            </div>
          ))
        )}
      </div>
        <button onClick={handleCheckout} className={styles["check-out-btn"]}>
          Proceed to Checkout
        </button>
      <C_Footer />
    </div>
  );
};

export default OM_C_Cart;
