import React, { createContext, useContext, useReducer, useEffect } from "react";

// To Context
const CartContext = createContext();

// Reducer  qun l trng thi gi hng
const cartReducer = (state, action) => {
  switch (action.type) {
    case "ADD_TO_CART":
      const existingItem = state.items.find(
        (item) => item.id === action.payload.id
      );
      if (existingItem) {
        return {
          ...state,
          items: state.items.map((item) =>
            item.id === action.payload.id
              ? { ...item, quantity: item.quantity + 1 }
              : item
          ),
        };
      } else {
        return {
          ...state,
          items: [...state.items, { ...action.payload, quantity: 1 }],
        };
      }
    case "UPDATE_QUANTITY":
      return {
        ...state,
        items: state.items.map((item) =>
          item.id === action.payload.id
            ? { ...item, quantity: action.payload.quantity }
            : item
        ),
      };
    case "REMOVE_FROM_CART":
      return {
        ...state,
        items: state.items.filter((item) => item.id !== action.payload.id),
      };
    case "CLEAR_CART":
      return { ...state, items: [] };
    default:
      return state;
  }
};

// Provider cho Context
export const CartProvider = ({ children }) => {
  // Ti trng thi t localStorage khi ng dng khi ng
  const storedCart = JSON.parse(localStorage.getItem("cart")) || { items: [] };

  const [state, dispatch] = useReducer(cartReducer, storedCart);

  // Lu trng thi vo localStorage mi khi `state` thay i
  useEffect(() => {
    localStorage.setItem("cart", JSON.stringify(state));
  }, [state]);

  return (
    <CartContext.Provider value={{ state, dispatch }}>
      {children}
    </CartContext.Provider>
  );
};

// Custom hook  s dng Context
export const useCart = () => useContext(CartContext);
